import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import axios from 'axios';
import { img_500, unavailable } from '../../Config/Config';
import { Button } from '@material-ui/core';
import YouTubeIcon from "@material-ui/icons/YouTube";
import './Contentmodel.css'
import Carousel from "../Carousel/Carousel"

const useStyles = makeStyles((theme) => ({
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    width: "90%",
    height: "80%",
    backgroundColor: "#39445a",
    border: "1px solid #282c34",
    borderRadius: 10,
    color: "white",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(1, 1, 3),
  },
}));

export default function ContentModal({children,media_type,id}) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [content,setcontent]=useState();
  const [video,setvideo]=useState();

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const fetchData=async()=> {
      const {data}= await axios.get(`https://api.themoviedb.org/3/${media_type}/${id}?api_key=bb88776a53ef123f749a726387d137f3&language=en-US`)
        setcontent(data);
       
        console.log(data)
    }

  const fetchvideo=async()=>{
      const {data}= await axios.get(`https://api.themoviedb.org/3/${media_type}/${id}/videos?api_key=bb88776a53ef123f749a726387d137f3&language=en-US`)
      setvideo(data.results[0]?.key )
      console.log(data)
    }

    useEffect(()=>{
         fetchData();
        fetchvideo();
        
    },[])

  return (
    <div>
      <div type="button" className="media" onClick={handleOpen}>
       {children}
      </div>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
         {content && ( <div className={classes.paper}>
            <div className="ContentModal">
            <img className="ContentModal__portrait" src={content.poster_path? `${img_500}/${content.poster_path}`: unavailable}
                  alt={content.name || content.title}
            />
            <img className="ContentModal__landscape" src={content.poster_path? `${img_500}/${content.backdrop_path}`: unavailable}
                  alt={content.name || content.title}
            />
            <div className="ContentModal__about">
                <span className="ContentModal__title">
                    {content.title || content.name}(
                    {(content.first_air_date || content.release_date || "----").substring(0,4)}
                    )
                </span>
                {content.tagline && (
                    <i className="tagline">{content.tagline}</i>
                )}
                <span className="ContentModal__description">
                    {content.overview}
                </span>
                <div className="ContentModal_carosel">
                    <Carousel media_type={media_type} id={id}/>
                </div>
                <Button
                    variant="contained"
                    startIcon={<YouTubeIcon/>}
                    color="secondary"
                    target="__blank"
                    href={`https://www.youtube.com/watch?v=${video}`}
                    className="button"
                >
                    Watch the trailer
                </Button>
            </div>
            </div>
          </div>)}
        </Fade>
      </Modal>
    </div>
  );
}
